.custom-time-picker {
    display: flex;
    gap: 10px;
    align-items: center;
    position: relative;
    &__input {
        font-size: 16px;
        background-color: #ffffff;
        border: 2px solid #eaeaf0;
        border-radius: 5px;
        min-height: 54px;
        width: 100%;
        padding: 10px 40px 10px 20px;
        transition: all 0.3s;
        &:focus {
            border: 2px solid #4878a6;
        }
    }
    &__inputAdornment {
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(50%);
    }
}
