.item-client {
    // .item-client__items
    &__items {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        @media (max-width: 600px) {
            display: flex;
            flex-direction: column;
            align-items: stretch;
        }
    }
