@import "../../../assets/styles/variables";
@import "../../../assets/styles/mixins";
.cart-products {
    // .cart-products__empty
    &__empty {
    }
    // .cart-products__list
    &__list {
    }
    // .cart-products__item
    &__item {
    }
}
.cart-product {
    display: flex;
    align-items: center;
    column-gap: 20px;
    padding: 30px 0;
    &:not(:last-child) {
        border-bottom: 1px solid #c8cacb;
    }
    &:first-child {
        padding-top: 0;
    }
    &:last-child {
        padding-bottom: 0;
    }
    @media (max-width: 600px) {
        flex-wrap: wrap;
        gap: 10px;
    }
    // .cart-product__link
    &__link {
        @include adaptive-font(16, 14, 0);
        line-height: 130%;
        color: #070c11;
        &:focus-visible {
            outline: 2px solid #2a5275;
            border-radius: 1px;
            transition: none;
        }
        // .cart-product__link--product-photo
        &--product-photo {
            flex: 0 0 80px;
            max-height: 80px;
            @media (any-hover: hover) {
                &:hover {
                    .cart-product__photo {
                        transform: scale(1.1);
                    }
                }
            }
            @media (max-width: 600px) {
                order: 1;
            }
        }
        // .cart-product__link--product-name
        &--product-name {
            flex: 1 1 240px;
            transition: all 0.3s;
            @media (any-hover: hover) {
                &:hover {
                    color: #4878a6;
                }
            }
            @media (max-width: 600px) {
                order: 2;
                flex: 1 1 100%;
                max-width: calc(100% - 150px);
            }
        }
    }
    // .cart-product__photo
    &__photo {
        width: 100%;
        object-fit: contain;
        transition: all 0.3s;
    }
    // .cart-product__counter
    &__counter {
        flex: 0 0 100px;
        @media (max-width: 600px) {
            order: 5;
        }
    }
    // .cart-product__prices
    &__prices {
        max-width: 90px;
        width: 100%;
        line-height: 130%;
        font-weight: 700;
        white-space: nowrap;
        @media (max-width: 600px) {
            order: 4;
            flex: 1 1 auto;
            max-width: unset;
            width: unset;
            @include adaptive-value(padding-left, 100, 90, 1);
        }
        @media (max-width: 500px) {
            padding-left: 40px;
        }
    }
    // .cart-product__old-price
    &__old-price {
        font-size: 16px;
        text-decoration: line-through;
        color: #838688;
        &::before {
            content: "$ ";
        }
    }
    // .cart-product__current-price
    &__current-price {
        font-size: 24px;
        color: #070c11;
        &::before {
            content: "$ ";
        }
    }
    // .cart-product__btn-delete
    &__btn-delete {
        background-color: transparent;
        cursor: pointer;
        color: #838688;
        transition: color 0.5s, transform 0.3s;
        &:focus-visible {
            outline: 2px solid #2a5275;
            border-radius: 1px;
            transition: none;
        }
        svg {
            color: inherit;
            pointer-events: none;
        }
        @media (any-hover: hover) {
            &:hover {
                color: rgb(204, 68, 68);
                transform: scale(1.2);
            }
        }
        @media (max-width: 600px) {
            order: 3;
            margin-left: auto;
            margin-right: 7px;
        }
    }
}
.cart-counter {
    display: flex;
    justify-content: space-between;
    height: 48px;
    padding-right: 20px;
    @media (max-width: 600px) {
        padding-right: 0;
    }
    // .cart-counter__btn
    &__btn {
        font-size: 20px;
        color: #070c11;
        font-weight: 700;
        padding: 0 5px;
        background-color: transparent;
        border-radius: 4px;
        border: 2px solid transparent;
        transition: background-color 0.3s, color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
                background-color: rgba(#4878a6, 0.1);
            }
        }
        &:disabled {
            opacity: 0.5;
            &:hover {
                color: #070c11;
                background-color: transparent;
                cursor: default;
            }
        }
        &:focus-visible {
            border: 2px solid #2a5275;
            transition: none;
        }
    }
    // .cart-counter__quantity
    &__quantity {
        flex: 0 0 48px;
        height: 48px;
        padding: 5px;
        max-width: 48px;
        text-align: center;
        border: 1px solid #c8cacb;
        border-radius: 4px;
        color: #070c11;
        margin: 0 5px;
    }
}
.removingItemTransition {
    &-enter {
        opacity: 0;
        transform: translateX(-100%);
    }
    &-enter-active {
        opacity: 1;
        transform: translateX(0);
        transition: all 0.5s ease-in;
    }
    &-exit {
        opacity: 1;
        transform: translateX(0) scale(1);
    }
    &-exit-active {
        opacity: 0;
        transform: translateX(-100%) scale(0);
        transition: all 0.5s ease-in;
    }
}
.cartEmptyTransition {
    &-enter {
        opacity: 0;
        transform: translateX(-100%) scale(0);
    }
    &-enter-active {
        opacity: 1;
        transform: translateX(0) scale(1);
        transition: all 0.5s ease-in;
    }
    &-exit {
        opacity: 1;
        transform: translateX(0);
    }
    &-exit-active {
        opacity: 0;
        transform: translateX(-100%);
        transition: all 0.5s ease-in;
    }
}
