.obtaining-method {
    &__list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: flex-end;
        gap: 20px;
        @media (max-width: 600px) {
            display: flex;
            flex-direction: column;
            align-items: stretch;
        }
    }
    &__item {
        &:last-child {
            grid-column: 1 / 3;
            .item-input {
                padding-bottom: 0;
            }
        }
    }
}
