.order-filled {
    display: flex;
    gap: 20px;
    align-items: center;
    @media (max-width: 500px) {
        flex-wrap: wrap;
        padding: 0 30px;
    }
    @media (max-width: 400px) {
        padding: 0;
    }
    // .order-filled__items
    &__items {
        flex-wrap: wrap;
        flex: 1 1 auto;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        column-gap: 20px;
        row-gap: 10px;
        @media (max-width: 500px) {
            margin-bottom: 20px;
        }
    }
    // .order-filled__item
    &__item {
        flex: 0 0 80px;
        max-height: 80px;
    }
    // .order-filled__img
    &__img {
        width: 100%;
        object-fit: contain;
    }
}
