@import "../../../assets/styles/variables.scss";
@import "../../../assets/styles/mixins.scss";
.summary {
    // .summary__body
    &__body {
        padding: 30px;
        background-color: #edf2f6;
        border-radius: 8px;
        margin-bottom: 20px;
        @media (max-width: 400px) {
            margin: 0 -10px 20px -10px;
            padding: 15px;
        }
    }
    // .summary__title
    &__title {
        line-height: 120%;
        color: #070c11;
        font-weight: 700;
        @include adaptive-font(28, 24, 0);
        @include adaptive-value(margin-bottom, 20, 10, 1);
    }
    // .summary__items
    &__items {
        padding-bottom: 20px;
        border-bottom: 1px solid #c8cacb;
    }
    // .summary__item
    &__item {
        display: flex;
        align-items: center;
        @include adaptive-font(20, 16, 0);
        line-height: 120%;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    // .summary__item-text
    &__item-text {
        flex: 1 1 auto;
        padding-right: 20px;
        color: #838688;
    }
    // .summary__item-value
    &__item-value {
        color: #070c11;
        &::before {
            content: "$ ";
        }
    }
    // .summary__total
    &__total {
        display: flex;
        align-items: baseline;
        padding-top: 30px;
        color: #070c11;
        margin-bottom: 20px;
    }
    // .summary__total-text
    &__total-text {
        flex: 1 1 auto;
        padding-right: 20px;
        font-weight: 600;
        @include adaptive-font(24, 20, 0);
        line-height: 120%;
    }
    // .summary__total-value
    &__total-value {
        font-weight: 700;
        @include adaptive-font(36, 24, 0);
        line-height: 130%;
        &::before {
            content: "$ ";
        }
    }
    // .summary__btn
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1 1 100%;
        width: 100%;
        min-height: 48px;
        padding: 16px 20px;
        background-color: #838688;
        border-radius: 4px;
        color: #ffffff;
        font-weight: 500;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #2a5275;
            }
        }
        &:disabled {
            pointer-events: none;
            background-color: rgba(#838688, 0.5);
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            transition: none;
        }
    }
}
