@import "../../../assets/styles/variables.scss";
@import "../../../assets/styles/mixins.scss";
.recipient-filled {
    &__list {
        list-style: none;
    }

    &__item-title {
        display: inline-block;
        padding-right: 10px;
        font-weight: 600;
        @include adaptive-font(20, 18, 0);
        line-height: 120%;
        color: #070c11;
    }
    &__item-description {
        display: inline-block;
        @include adaptive-font(20, 18, 0);
        line-height: 120%;
        color: #070c11;
        font-weight: 400;
    }
}
