@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";
.item-ordering {
    &.disabled {
        .item-ordering__title {
            margin-bottom: 0;
            color: #838688;
        }
        .item-ordering__body {
            padding: 30px;
            border: 1px solid #c8cacb;
            @media (max-width: 768px) {
                padding: 15px;
            }
        }
    }
    // .item-ordering__body
    &__body {
        padding: 30px;
        background-color: #ffffff;
        border: 1px solid #c8cacb;
        border-radius: 8px;
        margin-bottom: 20px;
        @media (max-width: 768px) {
            padding: 15px;
        }
        @media (max-width: 500px) {
            padding: 0;
            border: none;
        }
    }
    // .item-ordering__title
    &__title {
        @include adaptive-font(28, 20, 0);
        line-height: 120%;
        color: #070c11;
        font-weight: 700;
        margin-bottom: 30px;
    }
    // .item-ordering__content
    &__content {
    }
    // .item-ordering__btn
    &__btn-next {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        flex: 0 1 200px;
        min-width: 200px;
        padding: 14px 20px;
        background-color: #4878a6;
        border-radius: 4px;
        color: #ffffff;
        font-weight: 700;
        transition: background-color 0.3s;
        &:focus-visible {
            outline: 2px solid #000000;
            outline-offset: 2px;
            border-radius: 4px;
        }
        @media (any-hover: hover) {
            &:hover {
                background-color: #2a5275;
            }
        }
    }
    // .item-ordering__btn-change
    &__btn-change {
        flex: 0 0 200px;
        padding: 14px 20px;
        align-items: center;
        justify-content: center;
        color: #4878a6;
        font-weight: 700;
        border: 1px solid #4878a6;
        border-radius: 4px;
        background-color: #ffffff;
        transition: background-color 0.3s, color 0.3s;
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 4px;
            transition: none;
        }
        @media (any-hover: hover) {
            &:hover {
                background-color: #4878a6;
                color: #ffffff;
            }
        }
        @media (max-width: 500px) {
            flex: 1 1 100%;
        }
    }
    // .item-ordering__filled
    &__filled {
        display: flex;
        gap: 20px;
        align-items: center;
        @media (max-width: 500px) {
            flex-wrap: wrap;
        }
    }
    // .item-ordering__confirmed-preview
    &__confirmed-preview {
        flex: 1 1 auto;
    }
}

.changeStepTransition {
    &-enter {
        opacity: 0;
        transform: translateX(-100%);
    }
    &-enter-active {
        opacity: 1;
        transform: translateY(0);
        transition: all 0.5s ease-in;
    }
    &-exit {
        opacity: 1;
        transform: translateY(0);
    }
    &-exit-active {
        opacity: 0;
        transform: translateX(-100%);
        transition: all 0.5s ease-in;
    }
}
