@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.page-ordering {
    @include adaptive-value(padding-top, 40, 20, 1);
    padding-bottom: 40px;
    // .page-ordering__title
    &__title {
        font-weight: 700;
        line-height: 130%;
        color: #070c11;
        @include adaptive-font(36, 24, 0);
        @include adaptive-value(margin-bottom, 50, 20, 1);
    }
    // .page-ordering__form
    &__form {
    }
}
.ordering {
    display: flex;
    gap: 20px;
    @media (max-width: 1200px) {
        flex-direction: column;
    }
    // .ordering__content
    &__content {
        flex: 1 1 auto;
    }
    // .ordering__item
    &__item {
        &:not(:last-child) {
            margin-bottom: 40px;
            @media (max-width: 768px) {
                margin-bottom: 20px;
            }
        }
    }
    // .ordering__summary
    &__summary {
        flex: 0 0 420px;
        @media (max-width: 1200px) {
            flex: 1 1 auto;
        }
    }
}
