@import "../../../assets/styles/variables.scss";
@import "../../../assets/styles/mixins.scss";
.obtaining-method-filled {
    display: flex;
    gap: 20px;
    align-items: center;
    @media (max-width: 500px) {
        flex-wrap: wrap;
        padding: 0 30px;
    }
    @media (max-width: 400px) {
        flex-wrap: wrap;
        padding: 0;
    }
    // .obtaining-method-filled__content
    &__content {
        flex: 1 1 auto;
        padding-right: 30px;
        @media (max-width: 500px) {
            margin-bottom: 20px;
        }
    }
    // .obtaining-method-filled__title
    &__title {
        font-weight: 600;
        @include adaptive-font(24, 18, 0);
        line-height: 120%;
        color: #070c11;
        margin-bottom: 20px;
    }
    // .obtaining-method-filled__text
    &__text {
        @include adaptive-font(20, 16, 0);
        line-height: 120%;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }
    // .obtaining-method-filled__working-hours
    &__working-hours {
        font-size: 17px;
        line-height: 100%;
        color: #838688;
    }
    // .obtaining-method-filled__btn
    &__btn {
        flex: 0 0 200px;
        padding: 14px 20px;
        align-items: center;
        justify-content: center;
        color: #4878a6;
        font-weight: 700;
        border: 1px solid #4878a6;
        border-radius: 4px;
        background-color: #ffffff;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #4878a6;
                color: #ffffff;
            }
        }
        @media (max-width: 500px) {
            flex: 1 1 100%;
        }
    }
}
